
&overview {

    &:last-child {
        @include rem('margin-bottom', 80px);
    }

    .cards {

        > * {

            &:last-child() {

                > .card, > .image-card {
                    margin-bottom: 0;
                }
            }

            &.col-md-4 + .col-md-6 {
                margin-top: 0;
            }

            > .card {
                display: block;
                height: 100%;
                width: 100%;
                @include rem('margin-bottom', 30px);
                background-color: $color-light-gray;
                color: $color-black;

                > header {
                    display: inline-block;
                    position: relative;
                    height: 50px;
                    @include rem('margin', 15px 0);
                    @include rem('padding', 13px 30px);
                    background-color: $color-white;
                    @include skewie(50px, $color-white, "right");

                    
                    h3 {
                        position: relative;
                        font-family: $FrutigerLTStd-LightCn;
                        @include font-size(2.4);
                        line-height: 1;
                        color: $color-black;
                        padding: 0;

                        &:before {
                            display: none;
                        }
                    }
                }

                > div {
                    display: block;
                    color: $color-black;
                    @include rem('padding', 0 30px);
                    @include rem('margin-bottom', 30px);

                    p {
                        margin-bottom: 0;
                    }

                    span {
                        display: block;
                        position: relative;
                        @include rem('padding-left', 22px);

                        &:before {
                            content: '';
                            display: block;
                            position: absolute;
                            background: url('../images/chevron-black.svg') no-repeat top left;
                            left: 0;
                            @include rem('margin', 10px 11px 10px 4px);
                            height: 12px;
                            width: 7px;
                        }
                    }
                }
            }

            > a.card:hover, > a.card:focus {
                background-color: $color-red;
                text-decoration: none;
                outline: none;

                > div {
                    color: $color-white;

                    span {
                        text-decoration: underline;

                        &:before {
                            background: url('../images/chevron-white.svg') no-repeat top left;
                        }
                    }
                }
            }

            > .image-card {
                display: block;
                position: relative;
                @include rem('margin-bottom', 15px);
                background-color: $color-light-gray;
                color: $color-black;
                width: 100%;

                &:hover {
                    background-color: $color-red;
                    color: $color-black;
                    text-decoration: none;

                    > div {

                        &:after {
                            @include opacity('background-color', $color-red, 20);
                        }

                        img {
                            -webkit-filter: grayscale(1);
                        }
                    }

                    > header {

                        &:after {
                            background: url('../images/chevron-white.svg') no-repeat top left;
                        }

                        h3 {
                            color: $color-white;
                        }

                        h4 {
                            color: $color-white;
                        }
                    }
                }

                > header {
                    position: relative;
                    width: 100%;

                    @include rem('padding', 15px 10px 10px);

                    &:after {
                        content: '';
                        display: block;
                        position: absolute;
                        background: url('../images/chevron-black.svg') no-repeat top left;
                        top: 50%;
                        transform: translateY(-50%);
                        right: 0;
                        @include rem('margin-right', 10px);
                        height: 12px;
                        width: 7px;
                    }

                    h3 {
                        position: relative;
                        font-family: $FrutigerLTStd-LightCn;
                        @include font-size(2.0);
                    }

                    h4 {
                        color: $color-dark-gray;
                        font-family: $FrutigerLTStd-Roman;
                        @include font-size(1.2);
                    }
                }

                > div {
                    position: relative;

                    &:after {
                        display:block;
                        position:absolute;
                        content: '';
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                    }

                    img {
                        display: block;
                    }
                }
            }
        }
    }
}

/* Small devices (tablets, 768px and up) */
@media (min-width: $screen-sm-min) {
    &overview {

        &:last-child {
            @include rem('margin-bottom', 300px);
        }

        .cards {

            > * {

                &.col-md-4 + .col-md-6 {
                    margin-top: 0;
                }

                > .image-card {
                    @include rem('margin-bottom', 30px);

                    > header {

                        @include rem('padding', 12px 20px 10px);

                        &:after {
                            @include rem('margin-right', 20px);
                        }

                        h3 {
                            margin-bottom: 2px;
                        }
                    }
                }
            }
        }
    }
}

